
import { ApiRequest, API_URLS } from '@/api/request.api';
import { defineComponent, ref } from 'vue';
import { useRoute, useRouter } from 'vue-router';

export default defineComponent({
  name: 'Item',
  setup() {
    const item = ref({ id: '' });
    const loading = ref(true);
    const fetching = ref(false);
    const route = useRoute();
    const router = useRouter();
    const { productCode } = route.params;

    new ApiRequest(
      `${API_URLS.SEARCH_ITEM}?filter[product_code][_eq]=${productCode}`
    )
      .get()
      .execute()
      .then((req) => {
        // eslint-disable-next-line prefer-destructuring
        item.value = req.data[0];
      })
      .catch((err) => {
        throw new Error(err);
      })
      .finally(() => {
        loading.value = false;
      });

    const updateProduct = async () => {
      fetching.value = true;
      await new ApiRequest(`${API_URLS.UPDATE_ITEM}/${item.value.id}`)
        .patch()
        .body({
          ...item.value,
        })
        .execute()
        .catch((err) => {
          throw new Error(err);
        })
        .finally(() => {
          fetching.value = false;
          router.go(-1);
        });
    };

    return { item, loading, fetching, updateProduct };
  },
});
