      />
    </svg>
  </div>
  <div v-else>
    <button @click="$router.go(-1)">
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="h-10 w-10 text-green-500 hover:text-green-700"
        fill="none"
        viewBox="0 0 24 24"
        stroke="currentColor"
      >
        <path
          stroke-linecap="round"
          stroke-linejoin="round"
          stroke-width="2"
          d="M10 19l-7-7m0 0l7-7m-7 7h18"
        />
      </svg>
    </button>
    <div class="flex flex-col space-y-4">
      <div class="flex flex-col space-y-1">
        <label class="text-white text-sm" for="productCode">Produkt-Code</label>
        <input
          v-model="item.product_code"
          name="productCode"
          placeholder="Produkt-Code"
          class="
            rounded-3xl
            border border-gray-500
            focus:outline-none focus:ring focus:ring-green-500
            px-2
            py-1
            disabled:opacity-50
          "
          type="text"
        />
      </div>
      <div class="flex flex-col space-y-1">
        <label class="text-white text-sm" for="productName">Produktname</label>
        <input
          v-model="item.product_name"
          name="productName"
          placeholder="Produktname"
          class="
            rounded-3xl
            border border-gray-500
            focus:outline-none focus:ring focus:ring-green-500
            px-2
            py-1
            disabled:opacity-80
          "
          type="text"
        />
      </div>
      <label class="text-white text-sm flex items-center" for="item.is_gram">
        <input
          v-model="item.is_gram"
