        <label v-else class="text-white text-sm" for="productPrice"
          >Preis</label
        >
        <input
          v-model="item.price_tag"
          name="productPrice"
          :placeholder="item.is_gram ? 'Preis pro 100g' : 'Preis'"
          class="
            rounded-3xl
