        />
      </div>
      <div class="flex flex-col space-y-1">
        <label class="text-white text-sm" for="productMhd">MHD</label>
        <input
          v-model="item.good_till"
          name="productMhd"
          placeholder="MHD"
          class="
            rounded-3xl
            border border-gray-500
            focus:outline-none focus:ring focus:ring-green-500
            px-2
            py-1
            disabled:opacity-80
          "
          type="date"
        />
      </div>
      <div class="flex flex-col space-y-1">
        <label
          v-if="item.is_gram"
          class="text-white text-sm"
          for="productOldAmount"
          >Altbestand (Menge) in g</label
        >
        <label v-else class="text-white text-sm" for="productOldAmount"
          >Altbestand (Anzahl)</label
        >
        <input
          v-model="item.old_amount"
          name="productOldAmount"
          placeholder="Altbestand"
          class="
            rounded-3xl
            border border-gray-500
            focus:outline-none focus:ring focus:ring-green-500
            px-2
            py-1
            disabled:opacity-80
          "
          type="text"
        />
      </div>
      <div class="flex flex-col space-y-1">
        <label class="text-white text-sm" for="productOldMhd"
          >MHD (Altbestand)</label
        >
        <input
          v-model="item.old_good_till"
          name="productOldMhd"
          placeholder="Altbestand MHD"
          class="
            rounded-3xl
            border border-gray-500
            focus:outline-none focus:ring focus:ring-green-500
            px-2
            py-1
            disabled:opacity-80
          "
          type="date"
        />
      </div>
      <button
        :disabled="fetching"
        class="
          rounded
          bg-green-500
          hover:bg-green-600
          py-2
          px-3
          disabled:opacity-75
          text-white
        "
        @click="updateProduct()"
      >
        Aktualisieren
      </button>
    </div>
  </div>
</template>

<script lang="ts">
import { ApiRequest, API_URLS } from '@/api/request.api';
import { defineComponent, ref } from 'vue';
import { useRoute, useRouter } from 'vue-router';

export default defineComponent({
  name: 'Item',
  setup() {
    const item = ref({ id: '' });
    const loading = ref(true);
    const fetching = ref(false);
    const route = useRoute();
    const router = useRouter();
    const { productCode } = route.params;

    new ApiRequest(
      `${API_URLS.SEARCH_ITEM}?filter[product_code][_eq]=${productCode}`
    )
      .get()
      .execute()
      .then((req) => {
        // eslint-disable-next-line prefer-destructuring
        item.value = req.data[0];
      })
      .catch((err) => {
        throw new Error(err);
      })
      .finally(() => {
        loading.value = false;
      });

    const updateProduct = async () => {
      fetching.value = true;
      await new ApiRequest(`${API_URLS.UPDATE_ITEM}/${item.value.id}`)
        .patch()
        .body({
          ...item.value,
        })
        .execute()
        .catch((err) => {
          throw new Error(err);
        })
        .finally(() => {
          fetching.value = false;
          router.go(-1);
        });
    };

    return { item, loading, fetching, updateProduct };
  },
});
</script>
